
/**
 * 保留多为小数指令 v-keep-decimal="num"
 * 使用实例 <input v-model="value"  v-keep-decimal="2" type="number" />
 * @param num 要保留的小数位数 0 => 取整
 * @requires number
 **/

export default {
  bind (e, data) {
    // input类型不是number类型 进行拦截
    if (e.type !== 'number') return false
    if (+data.value > 0) { // data.value => 保留的小数位数
      // 初始
      e.value = e.value ? (+e.value).toFixed(data.value) : 0.00
      // 失焦后保留小数 data.value
      e.onblur = () => {
        e.value = e.value ? (+e.value).toFixed(data.value) : 0.00
      }
    } else if (+data.value === 0) { // 0 => 取整
      // 初始
      e.value = e.value ? parseInt(e.value) : 0
      // 取整
      e.onblur = () => {
        e.value = e.value ? parseInt(e.value) : 0
      }
    }
  }
}



