<template>
  <div class="">
     symbol
  </div>
</template>
<script>
export default {
  data () {
    return {}
  },
  mounted () {
    // 声明
    const s1 = Symbol('isSymbol')
    console.log(s1)
    /**
     * 作为对象的属性值
     * 无法被JSON.stringfy() 转换
     * 无法Object.keys() 枚举
     * for ... in 也无法枚举
     *   **/
    const obj = {
      [s1]: 64
    }
    console.log(obj[s1])
    this.handleTest('kk')
  },
  methods: {
    handleTest (type) {
      switch (type) {
        case 'test1': {
          console.log(12)
          break
        }
        case 'test2': {
          console.log(22)
          break
        }
        case 'test3': {
          console.log(32)
          break
        }
        default: break
      }
    }
  }
}
</script>



