import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index/index'
import eventBusTest from '@/views/eventBusTest/index'
import eventBusTest1 from '@/views/eventBusTest/test1'
import eventBusTest2 from '@/views/eventBusTest/test2'
import symbol from '@/views/symbol/index'
import keepDecimal from '@/views/keepDecimal/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/eventBusTest',
      name: 'eventBusTest',
      component: eventBusTest
    },
    {
      path: '/eventBusTest1',
      name: 'eventBusTest1',
      component: eventBusTest1
    },
    {
      path: '/eventBusTest2',
      name: 'eventBusTest2',
      component: eventBusTest2
    },
    {
      path: '/symbol',
      name: 'symbol',
      component: symbol
    },
    {
      path: '/keepDecimal',
      name: 'keepDecimal',
      component: keepDecimal
    }
  ]
})



