<template>
  <div class="">
    <p>{{form.name}}</p>
    <p>{{form.phone}}</p>
    <p>{{form.date}}</p>
    <router-link tag="button" :to="{ path: '/eventBusTest?type=test1'}">设置</router-link>
  </div>
</template>
<script>
import eventBus from '@/utils/eventBus'
export default {
  data () {
    return {
      form: {
        name: '',
        date: '',
        phone: ''
      }
    }
  },
  created () {
    // 设置绑定函数
    eventBus.$on('test1', function (data) {
      this.formData = data || {}
    })
    this.form = eventBus.formData
  },
  beforeDestroy () {
    eventBus.$emit('test1', {})
  }
}
</script>
<style lang="scss" scoped>
</style>



