// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import App from './App'

import keepDecimal from '@/directive/keepDecimal'

import { formatDisplayMpney } from '@/filter/showMoney'
Vue.use(ElementUI)

Vue.config.productionTip = false
// 全局指令
Vue.directive('keep-decimal', keepDecimal) // input 输入框保留多少位小数

// 全局过滤器
Vue.filter('formatDisplayMpney', formatDisplayMpney) // 科学显示金额 例如 11,123,123

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



