<template>
 <div class="map-box">
   <div class="map" id="map" ></div>
   <div class="yu-shope" @click="line">
     渔船号码： 4684466746669789797979
   </div>
 </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      map: null,
      loading: true
    }
  },
  created () {
    setTimeout(() => {
      this.initMap()
    }, 3000)
  },
  methods: {
    initMap () {
      this.map = new window.AMap.Map('map', {
        center: [113.5759735107, 22.1826363963],
        resizeEnable: true,
        zooms: [4, 30], // 设置地图级别范围
        zoom: 16
      })
      window.AMap.plugin(['AMap.ToolBar', 'AMap.Scale'], () => {
        this.map.addControl(new window.AMap.ToolBar({
          position: 'RB',
          liteStyle: false,
          ruler: false
        }))
        this.map.addControl(new window.AMap.Scale())
      })
      this.loading = false
    },
    // 绘制直线
    line () {
      const path = [
        new window.AMap.LngLat(113.5759735107, 22.1826363963),
        new window.AMap.LngLat(113.5807800293, 22.1794572429),
        new window.AMap.LngLat(113.5846853256, 22.1818813539),
        new window.AMap.LngLat(113.5805225372, 22.1837093443),
        new window.AMap.LngLat(113.5736131668, 22.1829145688),
        new window.AMap.LngLat(113.5802650452, 22.1764369807),
        new window.AMap.LngLat(113.5808658600, 22.1673758045)
      ]
      const polyline = new window.AMap.Polyline({
        path: path,
        zIndex: 10,
        showDir: true,
        dirColor: '#4ac3f9', // 线颜色
        strokeWeight: 10, // 线条宽度，默认为 1
        strokeColor: '#fbed72', // 线条颜色
        lineJoin: 'round' // 折线拐点连接处样式
      })
      this.map.add(polyline)
      var circleMarker = new window.AMap.CircleMarker({
        center: new window.AMap.LngLat(113.5759735107, 22.1826363963), // 圆心位置
        radius: 5, // 圆半径
        fillColor: 'red', // 圆形填充颜色
        strokeColor: 'red', // 描边颜色
        strokeWeight: 0, // 描边宽度
        zIndex: 11
      })
      var circleMarker1 = new window.AMap.CircleMarker({
        center: new window.AMap.LngLat(113.5805225372, 22.1837093443), // 圆心位置
        radius: 5, // 圆半径
        fillColor: '#fff', // 圆形填充颜色
        strokeColor: 'red', // 描边颜色
        strokeWeight: 2, // 描边宽度
        zIndex: 11
      })
      this.map.add(circleMarker)
      this.map.add(circleMarker1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  lang='scss' type='text/css'>
 @import '@/style/base.scss';
 .map-box{
   width: 100%;
   height: 100%;
   position: relative;
 }
 .map{
   width: 100%;
   height: 100%;
 }
 .yu-shope{
   position: absolute;
   z-index: 2020;
   width: 360px;
   height: 60px;
   background: #eaeaea;
   top: 0;
   right: 0;
   color: #333333;
   font-size: 16px;
   line-height: 60px;
   text-align: center;
 }
</style>



