<template>
  <div class="lay-content">
    <li>
      <span>
        姓名：
      </span>
      <input type="text" v-model="form.name" />
    </li>
    <li>
      <span>
        电话：
      </span>
      <input type="number" v-model="form.phone"  />
    </li>
    <li>
      <span>
        生日：
      </span>
      <input type="date" v-model="form.date"  />
    </li>
    <button @click="commit">
      保存
    </button>
  </div>
</template>
<script>
import eventBus from '@/utils/eventBus'
export default {
  data () {
    return {
      form: {
        name: '',
        date: '',
        phone: ''
      }
    }
  },
  computed: {
    type () {
      return this.$route.query.type || 'getdata'
    }
  },
  methods: {
    // 提交
    commit () {
      eventBus.$emit(this.type, this.form)
      this.$router.back(-1)
    }
  }
}
</script>
<style lang="scss" scoped>
.lay-content{
  width: 500px;
  min-height: 600px;
  border: solid 1px red;
  li {
    width: 100%;
    height: 60px;
    line-height: 80px;
    margin-bottom: 30px;
    text-align: left;
    span{
      font-size: 14px;
      margin-right: 30px;
    }
    input{
      display: inline-block;
      height: 40px;
      padding-left: 20px;
      width: 320px;
    }
  }
}
</style>



